
ul,
li {
  list-style: none;
}
html,
body,
ul,
li,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
}
body {
  background: #f7f7f7;
  position: relative;
  min-height: 100%;
}
.clear:after {
  content: "";
  clear: both;
  display: block;
}
.f-l {
  float: left;
}
.f-r {
  float: right;
}
.eps {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
#app {
  font-family: 'PingFang SC', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
}

.header {
  background: #EB3636;
  height: 44px;
  line-height: 44px;
  padding: 0 16px;
  position: relative;
}
.header .left-part {
  height: 24px;
  position: absolute;
  left: 16px;
  top: 10px;
  line-height: 24px;
}
.header .right-part {
  position: absolute;
  height: 44px;
  line-height: 44px;
  right: 10px;
  top: 0;
  padding: 0 10px;
}
.header h1 {
  text-align: center;
  font-size: 18px;
  line-height: 44px;
}
.red {
  color: #FF4A4A;
  font-weight: bold;
}
.title {
  line-height: 2.5;
}
table {
  font-size: 12px;
  width: 100%;
}
table caption {
  text-align: left;
  padding-left: 20px;
  line-height: 2;
  color: #000;
}
table:last-child caption {
  padding-top: 10px;
}
table th,
table td {
  line-height: 1.25;
}
.focus-btn {
  color: #fff;
  background: #FF4A4A;
  border-radius: 4px;
  padding: 0 16px;
  font-weight: bold;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  font-size: 18px;
}
.focus-btn:active {
  background: #EB3636;
}
.recommend {
  background: #FF4A4A;
  padding: 24px 0;
}
.recommend h2 {
  font-size: 16px;
  margin-bottom: 24px;
}
.recommend .features {
  /*padding: 0 60px;*/
}
.recommend .features li {
  width: 33%;
}
.recommend .features img {
  width: 48px;
  height: 48px;
  border-radius: 4px;
  margin-bottom: 4px;
}
.recommend .features .title {
  font-size: 14px;
}
.recommend .features .desc {
  font-size: 12px;
}
.bottom-border {
  padding-bottom: 20px;
  border-bottom: 1px solid #f1f1f1;
}
.focus-match {
  margin-top: 10px;
  background: #fff;
  color: #000;
}
.focus-match .tabs {
  font-size: 16px;
  height: 45px;
  line-height: 44px;
  padding: 0 16px;
  border-bottom: 1px solid #f1f1f1;
}
.focus-match .tabs .tab {
  width: 50%;
}
.focus-match .tabs .tab p {
  display: inline-block;
  padding: 0 16px;
  border-bottom: 2px solid #fff;
  font-weight: bold;
}
.focus-match .tabs .tab.active {
  color: #FF4A4A;
}
.focus-match .tabs .tab.active p {
  border-color: #FF4A4A;
}
.match-info {
  background: #fff;
  color: #000;
}
.match-info .betting-info {
  height: 36px;
  line-height: 36px;
  position: relative;
  font-size: 12px;
  color: #000;
  padding: 0 16px;
  border-bottom: 1px solid #f1f1f1;
}
.match-info .betting-info:before {
  content: "";
  background: #FF4A4A;
  width: 4px;
  height: 16px;
  position: absolute;
  left: 0;
  top: 8px;
}
.match-info .betting-info .team {
  text-align: left;
}
.match-info .betting-info .jackport {
  text-align: right;
}
.match-info .team-info {
  position: relative;
  height: 90px;
  padding: 17px 40px 0;
  /*border-bottom: 1px solid #f1f1f1;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.match-info .team-info .host-team,
.match-info .team-info .guest-team {
  width: 60px;
  position: absolute;
  top: 16px;
}
.match-info .team-info .host-team img,
.match-info .team-info .guest-team img {
  width: 60px;
  display: block;
  border: 1px solid #f8f8f8;
}
.match-info .team-info .host-team p,
.match-info .team-info .guest-team p {
  font-size: 12px;
  margin-top: 6px;
}
.match-info .team-info .host-team {
  left: 40px;
}
.match-info .team-info .guest-team {
  right: 40px;
}
.match-info .team-info .time-info .vs {
  font-size: 28px;
  font-weight: bolder;
  line-height: 1.5;
}
.match-info .team-info .time-info .deadline {
  font-size: 12px;
  line-height: 1.75;
}
.betting-ratio {
  padding: 4px 40px;
  height: 44px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.betting-ratio .ratio-host,
.betting-ratio .ratio-guest {
  height: 10px;
  position: relative;
}
.betting-ratio .ratio-host p,
.betting-ratio .ratio-guest p {
  position: absolute;
  width: 100%;
  top: 10px;
  text-align: center;
  font-size: 12px;
  line-height: 2;
}
.betting-ratio .ratio-host {
  background: #FF4A4A;
}
.betting-ratio .ratio-guest {
  background: green;
}
.betting-btn {
  margin-top: 4px;
  padding-left: 16px;
  padding-right: 16px;
}
.infomation p {
  font-size: 12px;
  line-height: 1.75;
  padding: 0 16px;
  text-align: left;
  margin-bottom: 20px;
}
.infomation p:last-child {
  margin-bottom: 0;
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
  height: 100%;
  color: #000;
  font-size: 14px;
  line-height: 1.75;
  text-align: left;
}
.mask .content {
  position: absolute;
  width: 90%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background: #fff;
  -webkit-box-shadow: 0 0 5px #ddd;
          box-shadow: 0 0 5px #ddd;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.mask .content .title {
  font-size: 18px;
  color: #000;
  height: 44px;
  line-height: 44px;
  border-bottom: 1px solid #f1f1f1;
  position: relative;
  padding: 0 16px;
}
.mask .content img {
  height: 16px;
  vertical-align: middle;
  margin-left: 4px;
}
.mask .content .content-inner {
  padding: 10px 16px;
}
.mask .content .content-inner code {
  border: 1px solid #ccc;
  background: #f1f1f1;
  width: 100%;
  display: block;
  margin: 10px 0;
  padding: 2px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  word-break: break-all;
}
.mask .content .content-inner .desc {
  font-size: 12px;
  color: #999;
}
.mask .content .content-inner .copy {
  margin: 10px 0;
  width: 100%;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
/* basic style */
.close {
  background: #f1f1f1;
  color: #000;
  border-radius: 15px;
  line-height: 30px;
  text-align: center;
  height: 30px;
  width: 30px;
  font-size: 24px;
  padding: 1px;
  font-weight: lighter;
}
/* use cross as close button */
.close::before {
  content: "\2716";
}
/* place the button on top-right */
.close {
  top: -15px;
  right: -15px;
  position: absolute;
}

.rule .rule-list[data-v-2941b223] {
  text-align: left;
  padding: 20px 20px;
  color: #333;
  font-size: 14px;
  line-height: 1.75;
}
.rule .rule-list li[data-v-2941b223] {
  padding: 10px 0;
}
